import React, { Component } from 'react'



export default class Input extends Component {
    render() {
        return (
            <div>
                <form> 
                <input type="text" placeholder="Enter a country name" onChange={this.props.selectCountry} style={{width:"300px",padding:"10px",margin:"10px"}}/>

                {this.props.details.length===1 ? <button style={{width:"200px",padding:"10px",margin:"30px",fontSize:"16px"}}>Back To Home Page</button> : null}

                <select onChange={this.props.selectRegion} style={{width:"300px",padding:"10px",fontSize:"16px",margin:"10px"}} >
    
                    <option value="region" >Select a region</option>
                    <option value="Asia" >Asia</option>
                    <option value="Africa">Africa</option>
                    <option value="Americas">Americas</option>
                    <option value="Europe">Europe</option>
                    <option value="Oceania">Oceania</option>
                    <option value="Polar">Polar</option>
                </select>
              
                </form>
            </div>
        )
    }
}

