import React, { Component } from 'react'


export default class SingleCountry extends Component {
    render() {
        return (
           
            <div style={{backgroundColor:"white", border:'solid 1px grey',margin:'5px' }}> 
 
                  <div  style={{ width: '10rem'}} onClick={this.props.detailedCountry.bind(this,this.props.country.name)}>
                                  <img width="100%" src={this.props.country.flag} alt=""/>
                                  <div style={{flex: 1}}>
                                      <h2>{this.props.country.name}</h2>
                                      <p><strong>Population:</strong><span style={{fontSize:12,color:'grey'}}>{this.props.country.population}</span></p>
                                      <p><strong>Region:</strong><span style={{fontSize:12,color:'grey'}}>{this.props.country.region}</span></p>
                                      <p><strong>Capital:</strong><span style={{fontSize:12,color:'grey'}}>{this.props.country.capital}</span></p>
                                  </div>
                 </div>
           
            </div>
           
        )
    }
}

