import React, { Component } from 'react'
import countriesAll from '../countriesAll.json'


export default class ShowDetail extends Component {

    
    render() {
        return (
            <div style={{backgroundColor:"white",margin:'0 auto',width:'35%'}}> 
 
            {this.props.details.map((item)=>{
                return(
                <div style={{backgroundColor:"white",margin:'5px'}}>
                    <img top width="100%" src={item.flag} alt=""/>
                                  <div style={{flex: 1}}>
                                      <h2>{item.name}</h2>
                                      <p><strong>Population:</strong><span style={{fontSize:12,color:'grey'}}>{item.population}</span></p>

                                      <p><strong>Region:</strong><span style={{fontSize:12,color:'grey'}}>{item.region}</span></p>

                                      <p><strong>Capital:</strong><span style={{fontSize:12,color:'grey'}}>{item.capital}</span></p>

                                      <p><strong>Languages:</strong><span style={{fontSize:12,color:'grey'}}>{item.languages.map(item=><div style={{display:"inline-block"}}>{`\u00A0\ ${item.name}`}</div>)}</span></p>

                                      <p><strong>Currencies:</strong><span style={{fontSize:12,color:'grey'}}>{item.currencies.map(item=>item.name)}</span></p>
                                      <br/>
                                      <br/>
                                    <p><strong>Borders:</strong>{countriesAll.map((b)=>{
                                     return  item.borders.includes(b.alpha3Code) ?
                                      <div style={{marginBottom:"50px"}}>
                                        
                                          <p>{b.name}</p> 
                                          <img top width="70%" src={b.flag} onClick={this.props.detailedCountry.bind(this,b.name)}></img>
                                      </div> : null
                                     
                                     })}</p>

                                  </div>
                                  
                </div>)
            })}

            
            </div>
        )
    }
}
