import React, { Component } from 'react'
import './App.css';
import countriesAll from './countriesAll.json'
import AllCountries from './components/AllCountries/AllCountries'
import Header from './components/Header'
import Input from './components/Input/Input'
import ShowDetail from './components/ShowDetail';






export default class App extends Component {

  state={
    countries:countriesAll,
    details:[]
  }





 selectRegion=(e)=>{
  this.setState({
    countries:countriesAll.filter((item)=>{
      return item.region===e.target.value || e.target.value==='region'
    })
  })
}



 selectCountry=(e)=>{
   
   this.setState({
    countries: countriesAll.filter((item) =>
      item.name.toLowerCase().includes(e.target.value.toLowerCase())
    ),
  });
 }

 detailedCountry=(name)=>{
  this.setState({
    countries:[],
    details: countriesAll.filter((item) =>
      item.name===name)
    
  })

  
 }
  render() {
    return (
      <div className="App">
        <Header />
        <Input countries={this.state.countries} selectRegion={this.selectRegion} selectCountry={this.selectCountry} details={this.state.details} />
        <ShowDetail details={this.state.details} countries={this.state.countries} detailedCountry={this.detailedCountry}/>
       <AllCountries countries={this.state.countries} detailedCountry={this.detailedCountry}/>
      </div>
    )
  }
}

